.register-box{
    width: 350px;
    height: 540px;
    background:white;
    border: 1px solid;
    border-color: white;
    top: 50%;
    left: 50%;
    position: absolute;
    transform: translate(-50%,-50%);
    box-sizing: border-box;
    padding: 70px 30px;
}
.avatar{
    width: 100px;
    height: 100px;
    border-radius: 50%;
    position: absolute;
    top: -50px;
    left: calc(50% - 50px);
}
h1{
    margin: 0;
    padding: 0 0 20px;
    text-align: center;
    font-size: 22px;
}
.register-box p{
    margin: 0;
    padding: 0;
    font-weight: bold;
}
.register-box input{
    width: 90%;
    margin-bottom: 10px;
}
.register-box input[type="text"], input[type="password"]
{
    border: none;
    border-bottom: 1px solid black;
    background: transparent;
    outline: none;
    height: 40px;
    font-size: 16px;
}
.register-box input[type="submit"]
{
    border: none;
    outline: none;
    height: 40px;
    background: #1c8adb;
    color: #fff;
    font-size: 18px;
    border-radius: 20px;
}
.register-box input[type="submit"]:hover
{
    cursor: pointer;
    background: #39dc79;
    color: #000;
}

.register-box a{
    text-decoration: none;
    font-size: 14px;
    color:black
}
.register-box a:hover
{
    color: #39dc79;
}

.error-message{
    color: red;
    font-size: 12px;
    margin-left: 4%;
}

.forgot-password, .submit{
    justify-content: center;
    text-align: center;
}





